<template>
  <div :class="$root.themeHomeChange === '1' ? 'basicMsgTable_light' : 'basicMsgTable'" id="basicMsgTable">
    <div class="header">合规检查任务基本信息</div>
    <div class="content">
      <table
        class="table"
        border="0"
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
      >
        <tr>
          <td class="tableHeader">单据编号：</td>
          <td>{{data.orderId}}</td>
          <td class="tableHeader">任务 / 单据名称：</td>
          <td>{{data.taskOrOrderName}}</td>
          <td class="tableHeader">检查单位：</td>
          <td>{{data.checkUnit}}</td>
          <td class="tableHeader">检查时间段：</td>
          <td>{{data.checkBeginDate && data.checkEndDate ? (data.checkBeginDate + '~' + data.checkEndDate) : '' }}</td>
        </tr>
        <tr>
          <td class="tableHeader">任务编号：</td>
          <td>{{data.taskId}}</td>
          <td class="tableHeader">单据日期：</td>
          <td>{{data.orderDate}}</td>
          <td class="tableHeader">被检查单位：</td>
          <td>{{data.checkedUnit}}</td>
          <td class="tableHeader">任务类别：</td>
          <td>{{ getTableOptionByName(data.taskType, 'name', 'id', 'taskType') }}</td>
        </tr>
        <tr>
          <td class="tableHeader">任务说明：</td>
          <td colspan="7">{{data.taskDesc}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
  export default {
    name: "basicMsgTable",
    components: {},
    props: {
      data:{
        type:Object,
        default:()=>{
          return {}
        }
      },
      loading:{
        type:Boolean,
        default:()=>{
          return false;
        }
      },
    },
    comments: {},
    data() {
      return {
        tableOption: {
          taskType: [
            { id: '1', name: "实时性检查" },
            { id: '2', name: "计划性检查" },
            { id: '3', name: "专项性检查" },
          ],
        }
      }
    },
    watch: {},
    mounted() {},
    methods: {
      getTableOptionByName(v, vName, checkName, vOptionName) {
        let res = this.tableOption[vOptionName].find( item => item[checkName] == v );
        return (res && res[vName]) || '';
      }
    }
  }
</script>

<style scoped lang="scss">
  .basicMsgTable {
    width: 100%;
    height: 150px;
    padding-top: 20px;
    display: flex;
    flex-wrap: wrap;
    // justify-content: space-between;
    // padding: 10px 0;

    .header {
      width: 100%;
      text-align: left;
      font-family: MicrosoftYaHei-Bold;
      font-size: 14px;
      color: #FFFFFF;
      font-weight: bold;
    }

    .content {
      width: 100%;
      height: calc(100% - 30px);
      margin-top: 15px;
      .table {
        width: 100%;
        height: 100%;
        font-size: 12px;
        color: #fff;
        font-weight: bold;
        border-collapse: collapse;

        tr {
          height: calc(100% / 3);
          background-color: #1A3066;
          border: 1.5px solid #232323;
        }

        td {
          height: calc(100% / 3);
          font-size: 12px;
          padding: 5px 10px;
          text-align: left;
          border-left: 1.5px solid #232323;
          // border-right: 1px solid #232323;
        }

        .tableHeader {
          width: 120px;
          height: 20px;
          background: #1E4178;
          font-size: 14px;
          color: #9FB6DC;
          text-align: right;
          pointer-events: 5px;
          font-weight: 400;
        }
        .warning {
          color: #FFC800;
        }
      }
    }
  }
  
  // 浅色主题
  .basicMsgTable_light {
    width: 100%;
    height: 150px;
    padding-top: 20px;
    display: flex;
    flex-wrap: wrap;
    // justify-content: space-between;
    // padding: 10px 0;
  
    .header {
      width: 100%;
      text-align: left;
      font-family: MicrosoftYaHei-Bold;
      font-size: 14px;
      color: #333;
      font-weight: bold;
    }
  
    .content {
      width: 100%;
      height: calc(100% - 30px);
      margin-top: 15px;
      .table {
        width: 100%;
        height: 100%;
        font-size: 12px;
        color: #333;
        font-weight: bold;
        border-collapse: collapse;
  
        tr {
          height: calc(100% / 3);
          background-color: #fff;
          border: 1.5px solid #ccc;
          border-top: 1.5px solid #ccc;
          border-bottom: -1.5px solid #ccc;
        }
  
        td {
          height: calc(100% / 3);
          font-size: 12px;
          padding: 5px 10px;
          text-align: left;
          border-left: 1.5px solid #ccc;
          // border-right: 1px solid #232323;
        }
  
        .tableHeader {
          width: 120px;
          height: 20px;
          background: rgba(67,137,249,0.16);
          font-size: 14px;
          color: #333;
          text-align: right;
          pointer-events: 5px;
          font-weight: 400;
        }
        .warning {
          color: #FFC800;
        }
      }
    }
  }
</style>
